export default {
	onShow() {
		console.log('onshow')
		// 调取判断会员接口
		uni.getStorage({
			key:'openId',
			success:res=>{
				this.openId = res.data
				this.requestUrl()
			}
		})
	},
	data() {
		return {
			isNext:false,
			openId:'',
			superiorId:'',
			selectedId: 0,
			topTitle: [{
				title: '终身会员',
				imgUrl: '../../static/vip/99@2x.png'
			}, {
				title: '普通会员',
				imgUrl: '../../static/vip/9.9@2x.png'
			}],
			isVip: '0',
			waitTime:'',
			TabCur: 0,
			tabList: [{
				title: '享免费更换外屏',
				yearTimes: '6次'
			}, {
				title: '享免费更换内屏',
				yearTimes: '1次'
			}, {
				title: '享免费更换主板、维修主板',
				yearTimes: '各1次'
			}, {
				title: '享免费更换电源、维修充电IC',
				yearTimes: '各1次'
			}, {
				title: '享免费更换前、后置摄像头、更换摄像头玻璃',
				yearTimes: '各1次'
			}, {
				title: '享免费更换WIFI模块、维修WIFI线路、更换数据线尾插',
				yearTimes: '各1次'
			}, {
				title: '享免费更换扬声器、麦克风、听筒、震动马达',
				yearTimes: '各2次'
			}, {
				title: '享免费更换开关排线、所有按键',
				yearTimes: '各1次'
			}, {
				title: '享免费更换手机后壳',
				yearTimes: '各1次'
			}, {
				title: '享免费更换、维修重力感应、指南针',
				yearTimes: '各1次'
			}],
		}
	},
	methods: {
		onPay() {
			uni.navigateTo({
				url: 'vip-type?id=' + this.selectedId,
				success: res => {
					console.log('success')
				}
			});
		},
		onSelected(e) {
			this.selectedId = e.currentTarget.id
		},
		bindChange(e){
			this.selectedId = e.detail.current
		},
		requestUrl(){
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/userInfo/isvip',
				method: 'GET',
				data: {
					openId: this.openId,
					superiorId: this.superiorId
				},
				success: res => {
					this.isVip = res.data.data
					this.waitTime = res.data.wait
					this.superiorId = res.data.superior_id
					uni.setStorage({
						key: 'superiorId',
						data: this.superiorId
					})
				},
				fail: err => {
					console.log(err)
				}
			})
		}
	}
}
